}

.radio-group {
  display: flex;
  margin: 22px auto;
  justify-content: space-between;
}

.radio-container {
  display: flex;
  align-items: center;
  margin-right: 17px;
}

.radio-label {
  margin-left: 8px;
  font-size: 16px;
  font-weight: 300;
  color: rgba(0, 31, 40, 0.9);
}

input[type="radio"] {
  cursor: pointer;
}
.saved-form{
  padding: 10px 38px 8px 38px;
  border: none;
  text-align: center;
  background-color: rgba(71, 88, 102, 1);
  font-size: 20px;
  font-weight: 700;
  text-decoration: none;
  color:rgba(245, 245, 245, 1);
  cursor: pointer;
}
.question-dropdown{
  width: 680px;
  margin: 25px 37px 25px 38px;
}
.dropdown-select{
  background-color: rgba(143, 170, 194, 0.2);
  width: 260px;
  height: 30px;
  margin: 20px auto 17px 29px;
  border: 1px solid rgba(71, 88, 102, 1);
}
.option-dropdown{
  background-color: rgba(143, 170, 194, 0.1);
  border: 1px solid rgba(0, 31, 40, 0.9);
  color: rgba(0, 31, 40, 0.9);
  font-size: 16px;
  padding: 6px 10px;
  padding-left: 42px;
  cursor: pointer;
  font-weight: 300;
}
.option-dropdown:not(:last-child) {
  border-bottom: 1px solid rgba(71, 88, 102, 1);
}

.option-dropdown:hover {
  background-color: rgba(71, 88, 102, 0.2); 
}
.fieldTitle{
