
.modal-overlay-field {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .modal-field {
    background: rgba(245, 245, 245, 1);
    width: 700px;
    height: 334px;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: "Merriweather", serif;
  }
  
  .modal-field h2{
    margin-top: 32px;
    font-weight: 700;
    font-size: 32px;
    color: rgba(0, 31, 40, 0.9);
  }
  
  .modal-field input{
    margin-top: 106px;
    margin-bottom: 48px;
    font-size: 20px;
    font-weight: 300;
  }
  
  .modal-field button{
    font-size: 20px;
    font-weight: 500;
  }