.my-forms h1{
    font-size: 36px;
    font-weight: 400;
    color: rgba(0, 31, 40, 0.9);
    margin: 48px 604px 75px 604px;
  }
  .create-new-form-myforms{
    display: flex;
    flex-direction: column;
    margin: 0px 35px 42px 0px;
  }
  .create-new-form-myforms span{
    text-decoration: none; 
    color: rgba(0, 31, 40, 0.9);
    font-size: 20px;
    font-weight: 400;
    padding: 0px;
    text-align: center;
  }
  .myform-block{
    position: relative;
    margin: 0px 35px 51px 0px;
    padding: 0px;
  }
  .test-link {
    padding: 0px;
    text-align: center;
    text-decoration: none;
  }
  .test-link span{
    color: rgba(0, 31, 40, 0.9);
    font-size: 20px;
    font-weight: 400;
    padding-left: 10px;
  }
  .test-myform{
    width: 300px;
    height: 200px;
    background-color: rgba(217, 217, 217, 1);
    cursor: pointer;
    transition: box-shadow 0.3s ease-in-out;
  }
  .test-myform:hover{
    box-shadow: 4px 4px 10px 3px rgba(0, 0, 0, 0.25);
  }
  .delete-test{
    display: block;
    opacity: 0;
    position: absolute;
    top: 10px;
    right: 10px;
    border: none;
    background-color: transparent;
    cursor: pointer;
    transition: opacity 0.3s
  }
  .myform-block:hover .delete-test { 
    opacity: 1; 
  }
  